@use "../../base.scss" as *;

.contact {
  margin-top: 10vh;
  height: calc(100vh - 10vh);

  &__heading {
    text-align: center;
    font-size: 4.8rem;
    padding: 0 0px 30px 0px;
  }

  &__subHeading {
    text-align: center;
    font-size: 2rem;
    padding: 0 30px;
  }

  &__container {
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__flex {
    margin: 40px 0px;
    display: flex;
    flex-direction: column;
    height: 50vh;
    justify-content: center;
    align-items: center;
  }

  &__icon {
    font-size: 13rem;
    padding: 20px;
    display: flex;
    justify-content: center;
  }

  &__icon:hover {
    color: $accent-color;
    cursor: pointer;
  }

  .emailText {
    font-size: 2rem;
    padding: 49px;
    display: flex;
    justify-content: center;
  }

  .emailText:hover {
    color: $accent-color;
    cursor: pointer;
  }
}

@media screen and (min-width: $breakpoint-tablet) {
  .contact__heading {
    font-size: 6rem;
  }

  .contact__subHeading {
    font-size: 4rem;
  }

  .contact__icon {
    font-size: 18rem;
  }
}

@media screen and (min-width: $breakpoint-laptop) {
  .contact__heading {
    font-size: 6rem;
  }

  .contact__subHeading {
    font-size: 3rem;
  }

  .contact__icon {
    font-size: 15rem;
  }
}
